@import url(https://fonts.googleapis.com/css2?family=Homemade+Apple&display=swap);
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

:root {
  --green: #64ffda;
  --green-tint: rgba(100,255,218,0.1);
  --navy: #0a192f;
  --lightest-navy: #172a45;
  --header-shadow: rgba(2,12,27,0.7);
  --slate: #8892b0;
  --light-slate: #a8b2d1;
  --lightest-slate: #ccd6f6;
  --white: #ffffff;
  --lightest-white: #e6f1ff;
  --pale-yellow: #ffda60;
  --font-mono: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
  --font-sans: "Calibre","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
}

body {
  font-family: "Calibre","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a192f;
  background-color: var(--navy);
  color: #ccd6f6;
  color: var(--lightest-slate);
}

.container {
  width: 70vw;
  max-width: 1000px;
  margin: 0 auto;
  height: 100%;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.green {
  display: inline-block;
  color: #64ffda;
  color: var(--green);
}

button {
  cursor: pointer;
}
.pointer {
  cursor: pointer;
}
.pointer:hover {
  color: #64ffda;
  color: var(--green);
}

@keyframes animateElement {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.zero-opac {
  opacity: 0;
}

.elementFadeup {
  opacity: 0;
  animation: animateElement 1.2s;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
}
.elementFadeup-1 {
  opacity: 0;
  animation: animateElement 1.2s;
  animation-delay: 0.4s;
  animation-fill-mode: forwards;
}
.elementFadeup-2 {
  opacity: 0;
  animation: animateElement 1.2s;
  animation-delay: 0.6s;
  animation-fill-mode: forwards;
}

@media only screen and (max-width: 700px) {
  p {
    font-size: 15px;
  }
  table {
    font-size: 13px;
  }
  .sm-container {
    width: 80vw;
  }
}
.default-btn {
    background-color: transparent;
    border: 1px solid #64ffda;
    border: 1px solid var(--green);
    border-radius: 4px;
    color: #ffda60;
    color: var(--pale-yellow);
    padding: 15px 30px;
    letter-spacing: 2px;
    outline: none;
    margin-top: 30px;
}





.App {
    position: relative;
}
.fixed-mail {
    display: block;
    position: fixed;
    bottom: 250px;
    left: -70px;
    transform: rotate(-90deg);
    z-index: 1;
}
.fixed-mail p {
    position: relative;
    font-family: var(--font-mono);
    font-size: 15px;
    letter-spacing: 0.6px;
    margin: 0;
}
.fixed-mail p:hover {
    color: var(--green);
}
.fixed-mail p:before {
    position: absolute;
    left: -140px;
    top: 8px;
    content: "";
    width: 0px;
    height: 1px;
    background-color: var(--slate);
    animation: widthAnimation 1.5s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
}
.fixed-follow {
    position: fixed;
    top: 150px;
    right: 50px;
    height: 170px;
    z-index: 1;
}
.fixed-follow ul {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.fixed-follow ul::before {
    position: absolute;
    content: "";
    left: 10px;
    top: -150px;
    height: 0px;
    width: 1px;
    background-color: var(--slate);
    animation: heightAnimation 1.5s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
}
.fixed-follow ul li :hover {
    fill: var(--green);
}

@keyframes widthAnimation {
    0% {
        width: 0%;
    }
    100% {
        width: 130px;
    }
}
@keyframes heightAnimation {
    0% {
        height: 0%;
    }
    100% {
        height: 150px;
    }
}

/* Mobile Media Queries */

@media only screen and (max-width:700px) {
    .fixed-mail, .fixed-follow {
        display: none;
    }
}
.header {
    height: 100px;
}
.fixed {
    height: 70px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: var(--navy);
    z-index: 1;
    box-shadow: 0px 10px 8px var(--header-shadow);
}
.hide {
    display: none;
}
.nav {
    display: flex;
    justify-content: center;
    align-items: center;
}
.list {
    display: flex;
    flex-direction: row;
}
.list-item {
    position: relative;
    margin: 0 10px;
    font: 15px var(--font-mono);
    opacity: 0;
    animation: fadeDown 0.5s;
    animation-fill-mode: forwards;
}

.list-item ::after {
    position: absolute;
    content: "";
    bottom: 0px;
    right: 0;
    width: 0px;
    height: 1px;
    background-color: var(--green);
    transition: width .5s;
}
.list-item :hover::after {
    width: 100%;
}
.list-item li {
    display: inline-block;
    padding-top: 10px;
}
.list-item p {
    display: inline-block;
    padding: 10px;
    color: var(--lightest-slate);
}

@keyframes fadeDown {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

.fadeDown-0 {
    animation-delay: 0s;
}
.fadeDown-1 {
    animation-delay: 0.2s;
}
.fadeDown-2 {
    animation-delay: 0.4s;
}
.fadeDown-3 {
    animation-delay: 0.6s;
}
.fadeDown-4 {
    animation-delay: 0.8s;
}

/* Menu Toggle animation svg */
.menu {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    padding: 0;
  }
  .line {
    fill: none;
    stroke: black;
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
      stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
  }
  .line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
  .opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
  }
  .opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
  .sm {
      display: none;
  }
  /* Mobile responsive code */

  @media only screen and (max-width: 700px) {
      .header {
          height: 55px;
      }
      .nav {
          display: none;
      }
      .sm {
          z-index: 1;
          width: 100%;
          display: flex;
          justify-content: flex-end;
          align-items: center;
        }
        .ham-menu {
          padding: 0 30px;
      }
      .menu-list {
          display: none;
          flex-direction: column;
          position: fixed;
          top: 0;
          left: 0;
          width: 100vw;
          height: 50vh;
          background-color: var(--navy);
          animation: fadeDown 1s;
          animation-fill-mode: forwards;
        }
        .cross {
          flex: 0.2 1;
          display: flex;
          justify-content: flex-end;
          padding: 0 30px;
        }
        .nav-sm-list {
            flex: 0.8 1;
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid var(--slate);
        }
        .list {
            flex-direction: column;
        }
        .disflex {
            display: flex;
        }

  }
.main {
    position: relative;
    width: 100%;
    min-height: calc(100vh - 100px);
    display: flex;
}
.main-content {
    position: relative;
    padding-top: 100px;
}
.main-content::before, .main-content::after {
    position: absolute;
    font-family: "Homemade Apple";
    color: var(--pale-yellow);
    opacity: .5;
}
.main-content::before {
    content: "<div>";
    top: 45px;
    left: -25px;
}
.main-content::after {
    content: "<div/>";
    bottom: -55px;
    left: -25px;
}

.heading-1 {
    font-size: 20px;
    color: var(--green);
    font-family: var(--font-mono);
    font-weight: 300;
    margin-left: 6px;
}

.heading-1, .heading-2, .heading-3, .text-1 {
    opacity: 0;
    animation: fadeUpAnimation 1.2s;
    animation-fill-mode: forwards;
}

.heading-2, .heading-3, .text-1 {
    margin-top: 7px;
}

.heading-2, .heading-3 {
    font-size: clamp(30px, 8vw, 80px);
}

.heading-2 {
    margin-top: 15px;
    font-weight: 600;
    color: var(--lightest-slate);
}

.heading-3 {
    font-weight: 500;
    color: var(--slate);
}

.text-1 {
    margin-left: 6px;
    margin-top: 15px;
    max-width: 500px;
    color: var(--slate);
    font-size: 20px
}
.mg-l-7 {
    margin-left: 7px;
}

@keyframes fadeUpAnimation {
    0% {
        opacity: 0;
        transform: translateY(40px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

.fadeup-1 {
    animation-delay: 0.6s;
}
.fadeup-2 {
    animation-delay: 0.8s;
}
.fadeup-3 {
    animation-delay: 1s;
}
.fadeup-4 {
    animation-delay: 1.2s;
}

@media only screen and (max-width:700px) {
    .main {
        align-items: center;
    }
    .main-content {
        padding: 0;
    }
    .text-1 {
        font-size: 17px;
    }
    .heading-1 {
        font-size: 17px;
    }
    .main-content::after, .main-content::before {
        display: none;
    }
}


.about-cont {
    padding: 50px 0;
}
.about-sec {
    display: flex;
    flex-direction: row;
    margin-top: 50px;
}
.about-right {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.about-right p {
    font-size: 18px;
    color: var(--slate);
    margin: 10px 0;
    display: inline-block;
}

.about-left {
    position: relative;
    width: 50%;
}
.image-holder {
    position: relative;
}
.my-image {
    width: 250px;
    height: 250px;
}
.square {
    width: 250px;
    height: 250px;
    box-sizing: border-box;
    border-radius: 7px;
    border: 2px solid var(--green);
    position: absolute;
    top: 0;
    left: 0;
    animation: rotateSquare 2s;
    animation-delay: 0.5s;
    animation-fill-mode: forwards;
}

@keyframes rotateSquare {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(45deg);
    }
}

/* Media Queries */

@media only screen and (max-width:700px) {
    .about-sec {
        flex-direction: column;
    }
    .about-right, .about-left {
        width: 100%;
    }
    .image-holder {
        margin-top: 60px;
    }
    .my-image, .square {
        width: 180px;
        height: 180px;
    }
    .about-right p {
        font-size: 15px;
    }
}
.section-heading {
    display: inline-block;
    text-align: center;
    margin: 40px;
}
.section-heading h1 {
    font-size: clamp(25px, 3vw, 45px);
    font-weight: 600;
    color: var(--lightest-slate);
}

.section-heading p {
    color: var(--green);
    margin-top: 10px;
    font-family: var(--font-mono);
}

@media only screen and (max-width:700px) {
    .section-heading {
        margin: 0;
    }
}

.skills {
    margin: 100px 0;
}
.skills-table {
    margin-top: 40px;
}
.skills-table-cont p {
    text-align: center;
}
.table-cont {
    position: relative;
    font-family: var(--font-mono);
}
.table-cont tr th, td {
    width: 150px;
    height: 30px;
    text-align: left;
    position: relative;
}
.table-cont tr th {
    color: var(--lightest-slate);
}
.table-cont tr td {
    color: var(--slate);
    padding-left: 10px;
}
.table-cont tr td::before {
    position: absolute;
    content: "›";
    top: 0;
    left: -5px;
    color: var(--green);
    font-size: 20px;
}

.table-cont tr th:hover, .table-cont tr td:hover {
    color: var(--green);
}

@media only screen and (max-width: 700px) {
    .skills-table-cont {
        margin-top: 30px;
    }
}


.experience {
    margin-top: 100px;
    height: 450px;
}
.exp-cont {
    margin: 0 auto;
    max-width: 700px;
}
.exp-bot {
    align-items: flex-start;
}
.exp-list {
    flex-direction: row;
}
.exp-list ul {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.exp-list ul li {
    height: 40px;
    display: block;
    cursor: pointer;
}
.exp-list ul li:hover {
    background-color: var(--green-tint);
    color: var(--green);
}
.exp-list ul li:hover > button {
    color: var(--green);
}
.exp-btn {
    width: 100%;
    height: 100%;
    background-color: transparent;
    padding-left: 0;
    border: none;
    outline: none;
    color: var(--lightest-slate);
    padding: 0 20px;
    text-align: left;
    font-family: var(--font-mono);
    font-size: 15px;
    cursor: pointer;
}

.button-active {
    color: var(--green);
    background-color: var(--green-tint);
}
.exp-cont-sec {
    padding-left: 20px;
}
.exp-head {
    font-weight: bold;
    font-size: 17px;
}
.exp-cont-date {
    color: var(--slate);
}
.exp-cont-list {
    position: relative;
    padding: 10px 0 0 20px;
}

.exp-cont-list li {
    position: relative;
    padding-top: 10px;
    color: var(--slate);
}
.exp-cont-list li p::before {
    position: absolute;
    content: "››";
    top: 5px;
    left: -18px;
    color: var(--green);
    font-size: 20px;
}

.display-none {
    display: none;
}

@media only screen and (max-width:700px) {
    .experience {
        height: 550px;
    }
    .exp-bot {
        flex-direction: column;
        margin-top: 30px;
    }
    .exp-list {
        width: 100%;
    }
    .exp-list ul {
        display: flex;
        flex-direction: row;
    }
    .exp-cont-main {
        margin-top: 30px;
    }
    .exp-cont-sec {
        padding: 0;
    }
}
.work {
    margin-top: 100px;
}
.work-cont {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 15px;
    padding-top: 30px;
}
.gig {
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--lightest-navy);
    border-radius: 7px;
}
.gig-top {
    margin-bottom: 25px;
}
.gig-links {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
}
.gig-heading {
    margin-bottom: 10px;
    color: var(--lightest-slate);
    font-family: var(--font-sans);
}
.publication {
    font-weight: bold;
    font-family: var(--font-mono);
    color: var(--green);
}
.repo-link {
    display: flex;
}
.link {
    margin-left: 15px;
}
.gig-text {
    color: var(--light-slate);
    /* line-height: 1.6; */
    font-family: var(--font-sans);
}
.gig-tech {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.gig-tech p {
    color: var(--slate);
    padding: 0 10px;
    font-size: 14px;
    font-family: var(--font-mono);
    color: var(--slate);
}
.gig-tech :nth-child(1) {
    padding: 0;
    padding-right: 10px;
}

.foot-cont {
    flex-direction: column;
    height: 70vh;
    text-align: center;
}
.foot-cont > * {
    margin-bottom: 30px;
}

.foot-cont p {
    color: var(--green);
    font-weight: bold;
    letter-spacing: 2px;
}

.foot-cont button:hover {
    background-color: var(--green-tint);
}

@media only screen and (max-width: 700px) {
    .footer {
        margin-top: 100px;
    }
}
